@import '../../../../../styles/templarios.abstracts';

.cart-item {
  display: flex;
  gap: 16px;
  align-items: center;

  &__image-container {
    display: flex;
    width: 200px;
    overflow: hidden;
    border-radius: 8px;

    @include breakpoint-down('md') {
      width: 150px;
    }

    @include breakpoint-down('sm') {
      width: 100px;
    }
  }

  &__middle {
    flex: 1;
  }

  &__price-container {
    display: flex;
    gap: 4px;
  }

  &__right {
    text-align: end;
  }
}

::slotted([slot='image']) {
  display: flex;
}

::slotted([slot='title']) {
  display: block;
  margin-bottom: 8px;
}

::slotted([slot='regular-price']) {
  margin-right: 8px;
  text-decoration: line-through;
}

::slotted([slot='save']) {
  display: block;
  margin-bottom: 8px;
}

::slotted([slot='short-description']) {
  display: block;
  margin-bottom: 16px;
}

::slotted([slot='plusminus']) {
  justify-content: flex-start !important;
  margin-bottom: 4px;
}

::slotted([slot='item-total']) {
  display: block;
}
