import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'tp-elem-cart-item',
  styleUrl: 'cart-item.scss',
  scoped: true,
})
export class TpElemCartItem {
  render() {
    return (
      <Host>
        <div class="cart-item">
          <div class="cart-item__left">
            <div class="cart-item__image-container">
              <slot name="image" />
            </div>
          </div>

          <div class="cart-item__middle">
            <slot name="title" />

            <div class="cart-item__price-container">
              <slot name="regular-price" />
              <slot name="price" />
            </div>

            <slot name="save" />
            <slot name="short-description" />
            <slot name="plusminus" />
            <slot name="remove" />
          </div>

          <div class="cart-item__right">
            <slot name="item-total" />
            <slot name="save-total" />
          </div>
        </div>
      </Host>
    );
  }
}
